.button{
    position: fixed;
    bottom: 30px;
    right: 30px;
    border: none;
    outline: none;
    background-color: var(--dark-color);
    color: #000;
    cursor: pointer;
    padding: 15px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    opacity: 0;
    z-index: 99;
}
.button{
    background-color: var(--bg-dark);
    transition: all 0.3s;
}
.show{
    opacity: 1;
    transition: opacity 0.3s ease-in;
}
.icon{
    font-weight: bold;
    color: white;
}