.form-control > :nth-child(odd){
  font-family: 'josefin sans',sans-serif;
  font-weight: 500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  border: 2px solid #ccc;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  outline: none;
  box-sizing: border-box;
  transition: all 0.3s;
}
.form-control > :nth-child(odd)::placeholder{
    color: #aaa;
}
.textarea{
    height: 150px;
    line-height: 150%;
    resize: vertical;
}
.button{
    font-family: "josefin sans",sans-serif;
    width: 120px;
    height: 45px;
    background-color: #000;
    color: #fff;
    border-radius: 5px;
    border: 0;
    cursor: pointer;
    font-size: 14px;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-top: -4px;
    transition: all 0.3s;
}
.button:hover{
    transform: scale(0.98);
}
.invalid input,
.invalid textarea{
    border: 1px solid var(--danger-color) !important;
    background-color: #fddddd;
}
.invalid input:focus,
.invalid textarea:focus{
  border-color: #ff8800;
  background-color: #fbe8d2;
}
.error{
    color: var(--danger-color);
}
@media screen and (max-width:600px){
    .button{
        width: 100%;
    }
}